body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  background-image: url("http://i.imgur.com/s5VFtxe.jpg");
  display: block;
  background-size: cover;
}

.title {
  color: rgb(244, 254, 255);
  text-shadow: 7px 7px 10px black;
  font-weight: bold;
  font-family: monospace;
  font-size: 3em;
}

.title2 {
  color: white;
  text-shadow: 3px 3px 6px black;
  font-weight: bold;
  font-family: sans-serif;
  font-size: 2.2em;
}

.title3 {
  color: white;
  text-shadow: 3px 3px 5px black;
  font-weight: bold;
  font-family: sans-serif;
  font-size: 1.8em;
}

.time {
  color: white;
  text-shadow: 2px 2px 2px black;
  font-weight: bold;
  font-family: sans-serif;
  font-size: 1.4em;
}

.weather {
  color: rgb(196, 231, 245);
  font-size: 2em;
  text-shadow: 2px 2px 2px black;
  font-weight: bold;
}

.weather2 {
  color: white;
  font-size: 1.2em;
  text-shadow: 1px 1px 3px black;
}

#list {
  list-style-type: none;
  color: white;
  text-align: left;
  margin: auto;
}

.fuck {
  height: 85%;
  width: 85%;
  margin: auto;
}
